import React, { useState, useCallback } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import InputBase from "@material-ui/core/InputBase";
import InputLabel from "@material-ui/core/InputLabel";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import Avatar from "@material-ui/core/Avatar";
import Grid from "@material-ui/core/Grid";
import { SettingsInputAntenna } from "@material-ui/icons";

import "../../styles/style.css";

import axios from "axios";
import MypageNav from "./MypageNav";
import { getUserInfo, updateUserInfo } from "../../actions/UserActions";

const useStyles = makeStyles((theme) => ({
  root: {
    position: "absolute",
    left: 0,
    right: 0,
    height: "95%",
    width: "100%",
    alignItems: "center",
    justifyContent: "center",
    backgroundColor: "#EF5941",
    maxWidth: "50em",
    margin: "auto",
  },
  root2: {
    position: "absolute",
    top: "10%",
    left: 0,
    right: 0,
    margin: "auto",
    height: "95.3%",
    width: "88%",
    alignItems: "center",
    backgroundColor: "white",
    borderRadius: "15px 15px 0 0",
    boxShadow: "2px 1px 5px 3px rgba(0,0,0, 0.7)",
    maxWidth: "70em",
  },
  avatar: {
    position: "absolute",
    top: "8%",
    right: "5%",
    width: theme.spacing(9),
    height: theme.spacing(9),
  },
  username: {
    position: "absolute",
    fontFamily: "BMDOHYEON",
    fontSize: "1.5em",
    top: "8%",
    left: "5%",
    // top: "12%",
    // left: "6.6%",
    maxWidth: "6em",
  },
  work_company: {
    position: "absolute",
    fontFamily: "BMJUA",
    top: "28%",
    left: "5%",
  },
  work_field: {
    position: "absolute",
    fontFamily: "BMJUA",
    top: "34%",
    left: "5%",
    color: "grey",
  },
  bio: {
    position: "absolute",
    transform: "translate(0, -3em)",
    fontFamily: "BMJUA",
    // top: "50%",
    left: "5%",
  },
}));

const style = {
  buttonGroup: {
    position: "absolute",
    display: "flex",
    left: 0,
    right: 0,
    bottom: "5.5%",
    margin: "auto",
    justifyContent: "center",
  },
  file: {
    fontWeight: "bold",
    position: "absolute",
    top: "23%",
    right: 0,
    overflow: "hidden",
  },
  button: {
    position: "relative",
    padding: "3px",
    width: "40%",
    maxWidth: "25em",
    margin: "auto 2%",
  },
};

const MypageEdit = (props) => {
  console.log("MypageEdit");

  const classes = useStyles();
  const history = useHistory();
  const user = props.routerInfo.location.state;

  console.log(user);

  const [image, setImage] = useState({});
  const [inputs, setInputs] = useState({
    username: user.username,
    profile_image: "https://www.ask2live.me" + user.profile_image,
    work_field: user.work_field,
    work_company: user.work_company,
    bio: user.bio,
  });

  const { username, profile_image, work_field, work_company, bio } = inputs;

  const dispatch = useDispatch();
  const onChange = useCallback((e) => {
    console.log("e.target.name", e.target.name);
    if (e.target.name === "image") {
      setImage({
        profile_image: e.target.files,
      });
      console.log("e.target.files", e.target.files);
    } else {
      const { name, value } = e.target;
      setInputs({
        ...inputs,
        [name]: value,
      });
    }
  });

  const onClick = async (e) => {
    e.preventDefault();

    const config = {
      headers: {
        "Content-Type": "multipart/form-data",
        Authorization: "Token " + localStorage.token,
      },
    };
    const data = {
      work_field: work_field,
      username: username,
      work_company: work_company,
      bio: bio,
    };
    console.log("data", data);
    const formData = new FormData();
    formData.append("work_field", data.work_field);
    formData.append("username", data.username);
    formData.append("work_company", work_company);
    formData.append("bio", bio);

    if (Object.keys(image).length != 0) {
      formData.append("profile_image", image.profile_image[0]);
    }

    console.log("====DATA====", formData);
    const resPatch = await axios.patch(
      "https://www.ask2live.me/api/user/update",
      formData,
      config
    );
    console.log(" ~", resPatch.data);
    dispatch(getUserInfo(localStorage.token));
    const resGet = await axios.get(
      "https://www.ask2live.me/api/user/read",
      config
    );
    console.log("  ", resGet);
    console.log("====DATA====", formData);

    history.push({
      pathname: "/mypage/" + username,
      state: resGet.data.detail,
    });
  };

  return (
    <>
      <MypageNav text={" "} />
      <div className={classes.root}>
        <div className={classes.root2}>
          <div>
            <p className={classes.username}>
              <input
                // className={classes.username}
                style={{
                  fontSize: "1em",
                  maxWidth: "6.9em",
                  border: "none",
                  fontFamily: "BMDOHYEON",
                  // borderBottom: "1px solid",
                  backgroundColor: "#FFEBE8",
                  borderRadius: "5px",
                }}
                required
                error
                defaultValue={username}
                placeholder=" "
                name="username"
                onChange={onChange}
              />
            </p>

            <Avatar
              className={classes.avatar}
              aria-label="recipe"
              src={profile_image}
            ></Avatar>
            <input
              style={style.file}
              type="file"
              name="image"
              // accept="image/*"
              onChange={onChange}
            />
          </div>

          <p className={classes.work_company}>
            <input
              // className={classes.work_company}
              style={{
                fontSize: "1em",
                border: "none",
                fontFamily: "BMJUA",
                // borderBottom: "1px solid",
                backgroundColor: "#FFEBE8",
                borderRadius: "5px",
              }}
              defaultValue={work_company}
              placeholder="  "
              name="work_company"
              onChange={onChange}
            />
          </p>
          <p className={classes.work_field}>
            <input
              // className={classes.work_field}
              style={{
                fontSize: "1em",
                border: "none",
                fontFamily: "BMJUA",
                color: "grey",
                backgroundColor: "#FFEBE8",
                borderRadius: "5px",
              }}
              defaultValue={work_field}
              placeholder="  "
              name="work_field"
              onChange={onChange}
            />
          </p>

          <div
            style={{
              position: "absolute",
              top: "54%",
              width: "100%",
              height: "40.5%",
            }}
          >
            <p className={classes.bio}>
              []
              <br />
            </p>
            <div className="bioWrapper">
              {/* <p style={{marginTop: 0}}> */}
              <textarea
                className="bioWrapper"
                style={{
                  border: "none",
                  width: "89%",
                  height: "80%",
                  // backgroundColor: "#F2AC57",
                  backgroundColor: "rgba(0, 0, 0, 0.05)",
                  borderRadius: "5px",
                }}
                defaultValue={bio}
                placeholder=" "
                name="bio"
                onChange={onChange}
              />
              {/* </p> */}
            </div>
          </div>

          <div style={style.buttonGroup}>
            <Button
              style={style.button}
              variant="outlined"
              color="primary"
              size="large"
              onClick={() => {
                history.goBack();
              }}
            >
              <span color="030916">
                
              </span>
            </Button>

            <Button
              style={style.button}
              variant="contained"
              color="primary"
              size="large"
              onClick={onClick}
            >
              <span color="030916">
                
              </span>
            </Button>
          </div>
        </div>
      </div>
    </>
  );
};

export default MypageEdit;
